
.title{
  font-size: 16px;
  font-weight: 800;
  font-family:微软雅黑;
  margin: 15px;
}
.grayText{
  margin: 15px;
  font-size: 13px;
  color:#8E8E90 ;
}
.lineSmall{
  width:100%;
  height:1px;
  background:rgb(226, 226, 226);
}
.imgBorder{
  width:100%;
  height: 400px;
  /*background: pink;*/
  margin-top: 5px;
  overflow: hidden;
}
.description{
  font-size: 15px;
  margin: 10px 0 0 15px;
}
.buttonBlue{
  width: 91%;
  height: 35px;
  margin: 15px auto;
  line-height: 35px;
  text-align: center;
  background: #007AFF;
  border-radius: 5px;
  color: #fff;
}
.companyName{
  color: #fff;
  font-size: 14px;
  background:#25B0E9 ;
  margin: 20px;
  width: 155px;
}
.introduceText{
  width: 86%;
  margin: 10px auto;
  text-align: justify;
  font-size: 14px;
}
.inputBox{
  width: 100%;
  height: 50px;
  background:#F8F8F8 ;
  border-top:1px solid #A2A2A2;
  position: fixed;
  bottom:50px;
  line-height: 46px;
}
.inputStyle{
  height: 25px;
  width: 270px;
  margin-left: 15px;
  text-align: center;
}
.judge{
  color: #267DD3;
  margin-left: 25px;
}



